import Head from "next/head";
import React from "react";
import useInitTransition from "../../../../hooks/useInitTransition";
import { ReadableTopTitle } from "../../../../components/atoms/ReadableTopTitle/ReadableTopTitle";
import { MainTextSourceHanSansJp } from "../../../../components/atoms/MainTextSourceHanSansJp/MainTextSourceHanSansJp";
import { PenguinExplanation } from "../../../../components/molecules/PenguinExplanation/PenguinExplanation";
import { Content } from "../../../../components/templates/Content/Content";
import size from "../../../../constants/size";
import { Mb } from "../../../../components/atoms/Mb"
import styled from "styled-components";
import customMedia from "../../../../styles/customMedia";
import { RoundImage } from "../../../../components/atoms/RoundImage/RoundImage";
import useSetHeader from "../../../../hooks/useSetHeader";

/**
 * Styled component.
 */
const Wrapper = styled.div`
  ${ customMedia.greaterThan("sp")`
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    & > div: last-child {
      width: calc(100% - 290px);
    }
  ` };
`;

/**
 * Functional component.
 * @param props 
 */
export default function Page({
  setTransTime
}) {
  useInitTransition(setTransTime);
  useSetHeader({
    color: "orange",
    prevHref: "/menus/menu2",
    nextHref: "/menus/menu2/act/2",
    pageProgress: {
      currentPage: 1,
      pageAmount: 6
    }
  });

  return (
    <>
      <Head>
        <title>ここに注意!海の特ちょうと海での行動を知ろう</title>
      </Head>
      <Content>
        <Mb size={ size.readableContentMB }>
          <ReadableTopTitle
            title={ <>メニュー２</> }
            subTitle={ <><ruby>海<rt>うみ</rt></ruby>の特ちょうと海での行動を知ろう</> }
          />
        </Mb>
        <Mb size={ size.readableContentMB }>
          <Wrapper>
            <Mb
              size={ {
                pc: 0,
                tb: 0,
                sp: size.readableContentMB.sp
              } }
            >
              <PenguinExplanation />
            </Mb>
            <MainTextSourceHanSansJp
              fontSize={ size.readableContentTextSize }
            >
              <ruby>海<rt>うみ</rt></ruby>は、地球でもっとも広い面積があり、天気によって一日のうちでも状況が大きく変化する水辺です。<br/>
              広い場所で変化も早いですが、人の目からは変化に気づきにくいです。<br/><br/>
              海の気象や変化の様子などの性質を知って、安全な行動を自分で選択できるようになりましょう。
            </MainTextSourceHanSansJp>
          </Wrapper>
        </Mb>
        <RoundImage
          image="m2_1-6_illust.png"
          ratio={ size.illustRatio }
        />
      </Content>
    </>
  )
}
